/* nav items */
.rc_nav {
  overflow: hidden;
  text-align: center;
  z-index: 6;
}

.rc_nav a {
  display: inline-block;
  position: relative;
  color: #d9d9d9;
  padding: 22px 25px;
  text-decoration: none;
  font-family: Poppins;
  font-size: 18px;
  transition: background 0.3s linear;
  z-index: 9;
}
.rc_nav a:after {
  background: none repeat scroll 0 0 transparent;
  content: "";
  display: block;
  height: 2px;
  /* start */
  left: 25%;
  position: absolute;
  background: #ffc400;
  transition: width 0.3s ease 0s, left 0.3s ease 0s;
  width: 0;
}
.rc_nav a:hover:after {
  width: 50%;
}
.rc_nav a:hover {
  color: #fff;
}

.rc_nav .icon {
  display: none;
}

